import Vue from 'vue';
import Component from 'vue-class-component'
import siderMenu from "@common/sider-menu";

@Component({
  components: {
    siderMenu
  },
})

export default class WorkManage extends Vue {
  columns = [
    {
      type: "selection",
      width: 50,
      align: "center"
    },
    {
      type: "index",
      width: 70,
      title: "序号",
      align: "center"
    },
    {
      title: "文件名",
      key: "textualName",
      align: 'center'
    },
    {
      title: "上传时间",
      key: "updatedTime",
      align: 'center',
      render: (h, params) => {
        return h(
          "div",
          this.dayjs(params.row.updatedTime).format("YYYY-MM-DD HH:mm:ss")
        );
      }
    },
    {
      title: "操作",
      key: "action",
      align: "center",
      slot: "action",
    }
  ];
  tabledata = [];
  searchValue = ''
  
  getTableList() {
    this.$api.TEXT_FIND_ALL('/findAll')
    .then(res => {
      console.log('dianwenliebiao', res.data.body);
      this.tabledata = res.data.body
    })
  };
  //模糊查询
  search() {
    this.$api.TextFindByName('/findByName', {
      textualName: this.searchValue
    }).then(res => {
      console.log('搜索列表', res.data.body);
      this.tabledata = res.data.body
    })
  };
  download(index) {
    this.$api.TEXT_DOWNLOAD('/download', {
      data: {
        wordId: this.tabledata[index].id
      },
      name: this.tabledata[index].textualName
    }) 
  }

  created() {
    if (this.$route.query.id) {
      this.listQuery.groupId = this.$route.query.id;
    }
    this.getTableList();
  }
}